import { message } from 'antd';
import axios from 'axios';

const onFinish = (values) => {
    if (!values.user.email) return;
    console.log(values.user.email)
    // 飞书机器人的Webhook地址
    const feishuWebhookUrl = 'https://open.feishu.cn/open-apis/bot/v2/hook/61d3b3d3-6a0e-4052-8285-41122a310e4d';

    // 设置消息接收人为目标邮箱地址
    const recipients = ['vivian.liu@autoagents.ai'];
    // const recipients = ['guanghui.chen@autoagents.ai'];

    // 使用Axios发送POST请求向飞书机器人发送消息
    axios.post(feishuWebhookUrl, {
        msg_type: 'text',
        content: {
            text: values.user.email,
        },
        user_ids: recipients,
    }, {
        headers: {
            Authorization: `Bearer qDBqNSbPJhv3gDDgdBaq7gkjdmrId2BM`, // 将你的Webhook访问令牌替换为实际的令牌
        },
    })
        .then((response) => {
            console.log('消息发送成功', response.data);
            message.success("您的申请提交成功，敬请期待");
        })
        .catch((error) => {
            console.error('消息发送失败', error);
        });
};
export default onFinish