import React from 'react'
import BottomSection from "../../BottomSection"
import imgVideo from '../../imgMove/视频图.webp'
import img1 from "../../imgMove/1.webp"
import img2 from "../../imgMove/2.webp"
import img3 from "../../imgMove/3.webp"
import style from "./style.module.css"
export default function HomePage() {
  return (
    <div className={style.main}>
      <div className={style.father}>
        <img src={imgVideo} alt="" />
        <span>自主智能体:</span>
        <span>来自未来的生产力</span>
      </div>
      <div className={style.language}>
        <span>基于企服领域大语言模型</span>
      </div>
      <div>
        <img src={img1} alt="" />
      </div>
      <div className={style.language}>
        <span>链接应用与数据</span>
      </div>
      <div>
        <img src={img2} alt="" />
      </div>
      <div className={style.language}>
        <span>助力亿万知识工作者10倍速提效</span>
      </div>
      <div>
        <img src={img3} alt="" />
      </div>
      <div className={style.languageP}>
        <p>&nbsp;</p>
        <p>我们来自国内领先人工智能、云计算公司，长期工作在服务企业数字化、智能化的第一线。</p>
        <p>&nbsp;</p>
        <p>一方面我们深知前沿技术发展的趋势、应用、边界，一方面我们对企业数智化转型的痛点、利好、困难有充分的认知和准备。</p>
        <p>&nbsp;</p>
        <p>我们愿意和客户、伙伴、员工一起，在AI时代的舞台上留下先行实践者的脚印。一切才刚刚开始。</p>
      </div>
      <BottomSection />
    </div>
  )
}
