import React from 'react'
import style from './style.module.css'
import src1 from "../../../../imgs/ico/1.png"
import src2 from "../../../../imgs/ico/2.png"
import src3 from "../../../../imgs/ico/3.png"
import src4 from "../../../../imgs/ico/4.png"
import src5 from "../../../../imgs/ico/5.png"
import src6 from "../../../../imgs/ico/6.png"
import src7 from "../../../../imgs/ico/7.png"
import src8 from "../../../../imgs/ico/8.png"
import src9 from "../../../../imgs/ico/9.png"
import src10 from "../../../../imgs/ico/10.png"
export default function AboutUs() {
    const imgS = [src1, src2, src3, src4, src5, src6, src7, src8, src9, src10];
    return (
        <div className={style.main}>
            <img className={style.mainBac} src="https://static.wixstatic.com/media/11062b_db8f5ddb085b41fda169efb854f9b69e~mv2.jpg/v1/fill/w_1899,h_1226,al_c,q_90,usm_0.66_1.00_0.01,enc_auto/11062b_db8f5ddb085b41fda169efb854f9b69e~mv2.jpg" alt="" />
            <div className={style.imgSrcLi}>
                <span id={style.animatedElement}>Meet our Team</span>
                <div className={style.spanLi}>
                    我们是一家基于大模型技术的人工智能公司，
                    专注于企业服务领域，
                    探索未来的价值创造新范式；
                    希望通过大模型驱动的智能工作方式，让10亿知识工作者能实现10倍效工作。
                    我们的团队成员来自全球领先的人工智能和云计算公司，
                    如阿里达摩院、腾讯、字节跳动、谷歌和微软等，团队长期致力于推动企业数字化和智能化的发展，
                    在大语言模型、多模态模型、自演进智能体、智能决策等领域有深厚技术积累；
                    团队同时拥有丰富的来自业务一线AI/大模型落地实战经验，团队成员服务过多家国内知名客户。
                </div>
            </div>
            <div className={style.imgSrcS}>
                {
                    imgS.map((item, index) => (
                        <div key={index}>
                            <img src={item} alt="" />
                        </div>
                    ))
                }
            </div>
        </div>

    )
}
