import React, { useState, useRef } from 'react'
import { NavLink } from 'react-router-dom';
import { MenuOutlined, CloseOutlined } from "@ant-design/icons"
import style from "./style.module.css"
export default function BulletFrame({ fnFlag }) {
    const overlayRef = useRef(null);
    const multiplexing = () => {
        fnFlag(true);
    }
    const childClick = (e) => {
        const title = document.querySelector("title");
        title.innerHTML = `${e.target.innerHTML} | Autoagents`;
        multiplexing()
    }
    const activeStyle = ({ isActive }) => ({ color: isActive ? "#57A7D8" : "#eeecec", lineHeight: "50px" });

    return (
        <div
            ref={overlayRef}
            className={style.overlay}>
            <div className={style.overlayIcon}>
                <div></div>
                <div style={{ width: "140px" }}></div>
                <CloseOutlined
                    onClick={multiplexing}
                    className={style.icon} />
            </div>
            <div className={style.navLinkStyle} onClick={childClick}>
                <NavLink style={activeStyle} to={'/'}>首页</NavLink>
                <NavLink style={activeStyle} to={'/产品'}>产品</NavLink>
                <NavLink style={activeStyle} to={'/关于我们'}>关于我们</NavLink>
                <NavLink style={activeStyle} to={'/加入我们'}>加入我们</NavLink>
            </div>
        </div>
    )
}
