import React, { useEffect, useRef, useState } from 'react'
import { Button, Form, Input } from 'antd';
import style from './style.module.css'
import videoSrc from '../../../../Video/20230904-191435.mp4';
import img1 from "../../../../imgs/1.webp"
import img2 from "../../../../imgs/2.webp"
import img3 from "../../../../imgs/3.webp"
import onFinish from "./onFinish"
const layout = {
    labelCol: {
        span: 8,
    },
    wrapperCol: {
        span: 16,
    },
};

const validateMessages = {
    required: '${label} is required!',
    types: {
        email: '${label} is not a valid email!',
        number: '${label} is not a valid number!',
    },
    number: {
        range: '${label} must be between ${min} and ${max}',
    },
};


export default function HomePage() {

    return (
        <div className={style.main}>
            <div className={style.displayCenter}>
                <div id={style.animatedElement}>
                    <span >&nbsp;&nbsp;自主智能体：</span>
                    <br />
                    <span>来自未来的生产力</span>
                </div>
                <div className={style.inputEmailFar}>
                    <div style={{ fontSize: "18px" }}>JOIN OUR WAITLIST</div>

                    <Form
                        {...layout}
                        name="nest-messages"
                        onFinish={onFinish}
                        style={{
                            maxWidth: 600,
                        }}
                        validateMessages={validateMessages}
                    >
                        <div className={style.inputEmail}>
                            <Form.Item
                                name={['user', 'email']}
                                label=""
                                rules={[
                                    {
                                        type: 'email',
                                    },
                                ]}
                            >
                                <Input
                                    style={{
                                        width: "283px",
                                        height: "55px",
                                        backgroundColor: "#c8a8d2",
                                        border: "1px solid #50b2cc",
                                        color: "#fff",
                                        fontWeight: "600",
                                        // zIndex: "-1"
                                    }}
                                    placeholder="请输入您的邮箱"
                                />
                            </Form.Item>
                        </div>
                        <div className={style.inputEmail}>
                            <Form.Item
                                wrapperCol={{
                                    ...layout.wrapperCol,
                                    offset: 8,
                                }}
                            >
                                <Button
                                    type="primary"
                                    htmlType="submit"
                                    className={style.buttonHover}
                                    style={{
                                        // zIndex:"-1",
                                        marginLeft: "-35px",
                                        width: "115px",
                                        height: "55px",
                                        // fontWeight:600,
                                        backgroundColor: "#A3D9F9",
                                        color: "black"
                                    }}>
                                    发送
                                </Button>
                            </Form.Item>
                        </div>
                    </Form>


                </div>
            </div>
            <div className={style.videoBox}>
                <video className={style.videoBackground}
                    preload="auto"
                    loop playsInline autoPlay
                    src={videoSrc}
                    tabIndex="-1"
                    muted="muted"
                ></video>
            </div>
            <div className={style.imgSrcS}>
                <div className={style.imgSrcLi}>
                    <span>基于企服领域大语言模型</span>
                </div>
                <div className={style.ingSrcTwo}>
                    <img src={img1} alt="" />
                </div>
                <div className={style.ingSrcTwo}>
                    <img src={img2} alt="" />
                </div>
                <div className={style.imgSrcLi}>
                    <span>链接应用与数据</span>
                </div>
                <div className={style.imgSrcLi}>
                    <span>助力亿万知识工作者10倍速提效</span>
                </div>
                <div className={style.ingSrcTwo} >
                    <img src={img3} alt="" />
                </div>
            </div>
            <div className={style.imgSrcStyle} >
            </div>
        </div>
    )
}
