import { useState, useEffect } from "react"
//pc端
import Topbar from './components/Topbar';
import Bottombar from "./components/Bottombar";
import MainPart from "./components/MainPart/MainPart";
// 移动端
import TopNavigation from "./pages/TopNavigationBar";
import MainBody from "./pages/MainBody";
import BulletFrame from "./pages/BulletFrame";
import Backward from "./pages/Backward";
import img from "./imgs/joini.jpg"
//移动端 结束
import './App.css';
function App() {
  const [scrollHeight, setScrollHeight] = useState(0);
  const [bulletFlag, setBulletFlag] = useState(true);
  // 监听滚动事件
  useEffect(() => {
    const handleScroll = () => {
      const currentScrollHeight = window.scrollY;
      setScrollHeight(currentScrollHeight);
    }
    window.addEventListener('scroll', handleScroll);
    // 在组件卸载时取消事件监听
    return () => {
      window.removeEventListener('scroll', handleScroll);
    };

  }, [])
  //获取分辨率
  const widthX = window.screen.width;
  const fnFlag = (e) => {
    setBulletFlag(e);
  }
  useEffect(() => {
    localStorage.setItem("img", img)
  }, []);
  return (
    <div className="App">
      {
        widthX < 1000 ? <>
          <TopNavigation fnFlag={fnFlag} bulletFlag={bulletFlag} />
          {
            bulletFlag ? <><MainBody />
              {
                scrollHeight > 500 ? <Backward scrollHeight={scrollHeight} /> : null
              }
            </> : <BulletFrame fnFlag={fnFlag} />
          }
        </> : <>
          <Topbar scrollHeight={scrollHeight} />
          <MainPart />
          <Bottombar />
        </>
      }

    </div>
  );
}

export default App;
